import seedrandom from 'seedrandom';
import { createRandomFloat } from 'pandemonium/random-float';
var RNG = seedrandom('nansi');
var randomFloat = createRandomFloat(RNG);
/**
 * Helper used to assess whether the given value is truly a number.
 */
function isNumber(value) {
    return typeof value === 'number' && !isNaN(value);
}
/**
 * Function used to "straighten" a graph by adding missing positions,
 * for instance.
 */
export default function straighten(graph) {
    var minX = Infinity;
    var maxX = -Infinity;
    var minY = Infinity;
    var maxY = -Infinity;
    var missingPositions = false;
    // Computing extents
    graph.forEachNode(function (node, attr) {
        if (isNumber(attr.x)) {
            if (attr.x < minX)
                minX = attr.x;
            else if (attr.x > maxX)
                maxX = attr.x;
        }
        else {
            missingPositions = true;
        }
        if (isNumber(attr.y)) {
            if (attr.y < minY)
                minY = attr.y;
            else if (attr.y > maxY)
                maxY = attr.y;
        }
        else {
            missingPositions = true;
        }
    });
    // Create random functions
    var randomX = RNG;
    var randomY = RNG;
    if (graph.order > 1 && minX !== Infinity) {
        randomX = randomFloat.bind(null, minX, maxY);
        randomY = randomFloat.bind(null, minY, maxY);
    }
    // Giving random positions to nodes that miss one
    if (missingPositions)
        graph.updateEachNodeAttributes(function (node, attr) {
            if (!isNumber(attr.x))
                attr.x = randomX();
            if (!isNumber(attr.y))
                attr.y = randomY();
            return attr;
        });
}
